import * as R from 'ramda';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { compose, mapProps } from 'recompose';
import { getVisDataDimensions, getVisTableLayout, getVisTableData } from '../../selectors/table';
import { getDimensionFormat, getDimensionGetter } from '../../selectors/vis';
import { getCustomAttributes } from '../../selectors/sdmx';
import { rules } from '@sis-cc/dotstatsuite-components';
import { DETableCell } from '@sis-cc/dotstatsuite-ui-components';
import Table from './Table';

const Cell = connect(createStructuredSelector({ variant: getDimensionGetter }))(DETableCell);

export default compose(
  connect(
    createStructuredSelector({
      dimensions: getVisDataDimensions(),
      customAttributes: getCustomAttributes,
      layoutIds: getVisTableLayout(),
      itemFormatter: getDimensionFormat,
      data: getVisTableData(),
      variant: getDimensionGetter,
    }),
  ),
  mapProps(
    ({
      chartData,
      viewerConfig,
      data,
      customAttributes,
      dimensions,
      layoutIds,
      itemFormatter,
      variant,
    }) => ({
      viewerConfig,
      data,
      layoutIds,
      itemFormatter,
      renderObservation: rules.observationGetter(Cell)({
        observations: R.prop('observations', data),
        dimensions,
        customAttributes,
        isRtl: false,
      })(R.identity),
      // little dirty for now, in the wait of a better common mgmt of table and charts
      header: R.pick(['title', 'subtitle', 'uprs'], chartData),
      footnotes: R.prop('footnotes', chartData),
      isNotATable: R.pipe(R.prop('observations'), R.values, R.length, R.equals(1))(data),
      variant,
    }),
  ),
)(Table);
